import React, { Component } from 'react';

class Success extends Component {
  render() {
    if(this.props.email){
      return (
      <div className="container text-center">
        <div className="Success Component contact-form col-sm-9 col-md-9 col-xs-9 text-center">
          <div className="output_message_holder d-block">
            <p className="output_message success">Votre inscription a t prise en compte !</p>
          </div>
          <p> Un email vous a t envoy  l'adresse <b>{this.props.email}</b></p>
          <a className="btn btn-primary" href="/">Retour  la page d'accueil</a>
        </div>
      </div>
      );
    } else if (this.props.activated) {
      return (
        <div className="container text-center">
          <div className="Success Component contact-form col-sm-9 col-md-9 col-xs-9 text-center">
            <meta http-equiv="refresh" content="3; url=/" />
              <div className=" output_message_holder d-block">
                <p className="output_message success">Votre compte a t activ !</p>
              </div>
            <p> Vous allez tre redirig sur la page d'accueil</p>
            <a className="btn btn-primary" href="/">Retour  la page d'accueil</a>
          </div>
        </div>
      );
    } else if (this.props.forgotPassword) {
      return (
        <div className="container text-center">
          <div className="Success Component contact-form col-sm-9 col-md-9 col-xs-9 text-center">
            <div className=" output_message_holder d-block">
              <p className="output_message success">L'email de rinitialisation du mot de passe a bien t envoy</p>
            </div>
            <p> Veuillez cliquer sur le lien envoy par mail pour procder  la rnitialisaiton de votre mot de passe</p>
            <a className="btn btn-primary" href="/">Retour  la page d'accueil</a>
          </div>
        </div>
      );
    } else if (this.props.newPassword) {
      return (
        <div className="container text-center">
          <div className="Success Component contact-form col-sm-9 col-md-9 col-xs-9 text-center">
            <div className="row">
              <div className="output_message_holder d-block">
                <p className="output_message success">Votre mot de passe a t chang !</p>
              </div>
            </div>
            <div className="row">
              <div className="col-sm-12 col-md-12 col-xs-12 text-center output_message_holder d-block">
                <a href="/">Retour  la page d'accueil</a>
              </div>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default Success;



